<script>
import { loop } from '@/api'
export default {
  data () {
    return {
      netData: {
        new: {},
        top: {}
      },
      timer: null
    }
  },
  methods: {
    loop () {
      loop()
        .then(res => {
          this.netData = res.data
        })
    }
  },
  created () {
    this.loop()
    this.timer = setInterval(() => {
      this.loop()
    }, 10000)
  },
  props: {
    tabbarNameList: {
